@import '../../scss/color-var.scss';

.line-decor {

  position: absolute;
  // display: flex;
  height: 50px;
  width: 75%;
  top: 150px;
  left: 470px;

  .line-red {
    position: absolute;
    height: 7px;
    width: 80%;
    transform: skew(140deg);
    background: linear-gradient(90.05deg, $red 38.38%, rgba(222, 10, 20, 0.265625) 87.59%, rgba(222, 10, 20, 0) 101.26%);
    // @media only screen and (max-width: 1500px) {
    //   width: 80%;
    // }
  }

  @media only screen and (max-width: 1550px) {
    width: 65%;
  }
  @media only screen and (max-width: 1150px) {
    display: none;
  }

  .first-line {
    position: relative;
    top: 25px;
    right: 180px;
  }

  .second-line {
    position: relative;
    right: 70px;
  }

  .third-line {
    position: relative;
    bottom: 25px;
    left: 45px;
  }

}